import React, { lazy,Suspense,Component } from 'react'
import {
  TheContent,
  TheSidebar,
  TheFooter,
  TheHeader,
  TheHeaderDropdown,
  HeaderVisiteur,
  SidebarVisiteur,
  SidebarProprietaire,
  SidebarEntreprise,
} from './index'

class TheLayout extends Component {
  constructor(props) {
      super(props)
      this.state = {
        layout:""
      }   
      
  }
  componentDidMount() {
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    try{
      if(data.role=="ADMIN"){
        this.setState({layout:<TheSidebar/>})
      }else{
        if(data.role=="PROPRIETAIRE_TERRAIN"){
          this.setState({layout:<SidebarProprietaire/>}) 
        }else{
          this.setState({layout:''})
        }
      }
    }catch(error){

    }
  }
  render(){

  return (
    <div className="c-app c-default-layout">
     {this.state.layout}
      <div className="c-wrapper">
        <TheHeader/>
        {/* <HeaderVisiteur/> */}
        <div className="c-body">
          <TheContent/>
        </div>
        <TheFooter/>
      </div>
    </div>
  )
  }



}
export default TheLayout
