import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
} from '@coreui/react'

import CIcon from '@coreui/icons-react'

// sidebar nav config
import navigation from './_nav'

const TheSidebar = () => {
  const dispatch = useDispatch()
  const show = useSelector(state => state.sidebarShow)

  var bgColors = { 
    "Default": "#81b71a",
    "Blue": "#173084",
    "Cyan": "#37BC9B",
    "Green": "#4f8f3f",
    "Red": "#E9573F",   
    "Yellow": "#F6BB42",
  };

  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({type: 'set', sidebarShow: val })}
      size="sm"
      colorScheme="dark"
      style={{backgroundColor: bgColors.Blue}}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        <CIcon
          className="c-sidebar-brand-full"
          // name="logo-negative"
          height={35}
        />
        <CIcon
          className="c-sidebar-brand-minimized"
          // name="sygnet"
          height={35}
        />
      </CSidebarBrand>

      <CSidebarNav
        style={{backgroundColor: bgColors.Blue}}
      >

        <CCreateElement
          items={navigation}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle
          }}
          style={{backgroundColor: bgColors.Green}}
        />
      </CSidebarNav>

      <CSidebarMinimizer className="c-d-md-down-none"/>
      
    </CSidebar>
  )
}

export default React.memo(TheSidebar)
