import React, { Component }  from 'react'
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {
 Badge,
 Row,
 Col,
} from "reactstrap"
import  { Redirect } from 'react-router-dom'

import logo from '../assets/bollky.jpg'

class TheHeaderDropdown extends Component {

    constructor(props) {
      super(props)
      this.state = {
         ok:'',
         user:{}
      }
    }
    componentDidMount() {
  
      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      try{
        this.setState({user: JSON.parse(localStorage.getItem('token_auth')).user});
      }catch(err){
        
        
      }

    }
  handleDeconnexion=()=>{
    localStorage.removeItem('token_auth');
    this.setState({ok: <Redirect to='/accueil-visiteu'  />});
    
  }
render() {
  const acceuil=this.props.acceuil
  return (
    <CDropdown   
      inNav
      className="c-header-nav-items mx-2"  
      direction="down"          
    >
    {this.state.ok}
    <Row>
      <Col className="mt-3 mr-4">  
      { !acceuil?
        
        <Badge  style={{backgroundColor:"#5bab26"}} color="info">
          
        {this.state.user.nom?this.state.user.prenom.toUpperCase():null} {this.state.user.nom?this.state.user.nom.toUpperCase():null} 
      </Badge>:null
      }
      </Col>
      <Col className="ml-3">   
        <CDropdownToggle className="c-header-nav-link" caret={false}>
          <div className="c-avatar">
            <CImg
              src={logo}
              className="c-avatar-img"
              alt="image profil"
            />
          </div>
        </CDropdownToggle>
      </Col>
    </Row>
    

      <CDropdownMenu className="pt-0" placement="bottom-end">
      { acceuil?
          <CDropdownItem >
          
            <span style={{color:"green"}}>{this.state.user.nom?this.state.user.prenom.toUpperCase():null} {this.state.user.nom?this.state.user.nom.toUpperCase():null}</span>
         </CDropdownItem>:null
        }
        <CDropdownItem divider />
        { acceuil?
          <CDropdownItem href="/#/login" >
          <CIcon name="cil-home" className="mfe-2" />
            Mon espace
         </CDropdownItem>
         : 
         <CDropdownItem href="/#/modifierpwd" >
         <CIcon name="cil-lock-locked" className="mfe-2" />
           Changer votre mot de passe
        </CDropdownItem>
       }
        <CDropdownItem divider />

        <CDropdownItem onClick={this.handleDeconnexion} >
          <CIcon name="cil-lock-locked" className="mfe-2" />
            Déconnexion
        </CDropdownItem>
       

      </CDropdownMenu>

    </CDropdown>
  )
}
 }
export default TheHeaderDropdown
