import React, { Suspense } from 'react'
import {
  Redirect,
  Route,
  Switch
} from 'react-router-dom'
import { CContainer, CFade } from '@coreui/react'

// routes config
import routes from '../routes'
  
const loading = (
  <div className=" text-center">
    <div className="sk-spinner sk-spinner-pulse" style={{ marginTop: '5rem' }}></div>
  </div>
)

let controle=false
// let controle=true
let acces={}
let ma_route=[]
// let ma_route = routes
acces=JSON.parse(localStorage.getItem('token_auth'))
 try{
   if(acces.role=="ADMIN" || acces.role=="PROPRIETAIRE_TERRAIN" || acces.role=="VISITEUR" ){
    ma_route=routes
    controle=true
    console.log("okkkkkk")

  }

 }catch(error){
  ma_route=[]
 }
console.log("=====",acces)
const TheContent = () => {
  return (
    <main className="c-main">
      <CContainer fluid>
        <Suspense fallback={loading}>
          <Switch>
            {ma_route.map((route, idx) => {
              return route.component && (
                <Route
                  key={idx}
                  path={route.path}
                  exact={route.exact}
                  name={route.name}
                  render={props => (
                    <CFade>
                      <route.component {...props} />
                    </CFade>
                  )} />
              )
            })}
            {
              (controle)?
                (<Redirect from="/" to="/accueil-visiteur" />):null
            }
            {
            (!controle)?
            (<Redirect  to="/accueil-visiteur" />):null
            }
            
          </Switch>
        </Suspense>
      </CContainer>
    </main>
  )
}

export default React.memo(TheContent)
